/* ----------------------------------- COLORS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-family: "Poppins", sans-serif;
  color: hsl(234deg, 12%, 34%);
  font-weight: 600;
  font-size: 15px;
}

body {
  background-color: hsl(0deg, 0%, 98%);
  display: flex;
  justify-content: center;
  align-items: center;
}

main {
  margin: 12.5vh 0;
  width: calc(100% - 60px);
}
main h1 {
  font-size: 1.7rem;
  margin-bottom: 1rem;
  text-align: center;
}
main h1 span {
  display: block;
  font-weight: 200;
}
main .description {
  color: hsl(229deg, 6%, 66%);
  font-weight: 400;
  text-align: center;
  margin-bottom: 5rem;
}

.cards-container {
  display: grid;
  gap: 2rem;
}
.cards-container .card {
  padding: 1.5rem 1.9rem;
  border-radius: 5px;
  box-shadow: 0px 8px 20px 5px #e6e6e6;
  position: relative;
}
.cards-container .card h2 {
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
}
.cards-container .card .role {
  font-size: 0.9rem;
  font-weight: 400;
  color: hsl(229deg, 6%, 66%);
  margin-bottom: 6rem;
  line-height: 1.5rem;
}
.cards-container .card img {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(-1.9rem, -1.5rem);
}
.cards-container .card:nth-child(1) {
  border-top: 4px solid hsl(180deg, 62%, 55%);
}
.cards-container .card:nth-child(2) {
  border-top: 4px solid hsl(0deg, 78%, 62%);
}
.cards-container .card:nth-child(3) {
  border-top: 4px solid hsl(34deg, 97%, 64%);
}
.cards-container .card:nth-child(4) {
  border-top: 4px solid hsl(212deg, 86%, 64%);
}

@media screen and (min-width: 768px) {
  body {
    height: 100vh;
  }
  main {
    max-width: 1110px;
    margin: 0;
  }
  main h1 {
    margin-bottom: 1.3rem;
    font-size: 2.4rem;
    line-height: 3.3rem;
  }
  main .description {
    max-width: 500px;
    margin: 0 auto 4.2rem;
  }
  .cards-container {
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
  }
  .cards-container .card .role {
    margin-bottom: 8rem;
  }
  .cards-container .card:nth-child(1) {
    grid-column: 1/2;
    grid-row: 1/3;
  }
  .cards-container .card:nth-child(2) {
    grid-column: 2/3;
  }
  .cards-container .card:nth-child(3) {
    grid-column: 2/3;
  }
  .cards-container .card:nth-child(4) {
    grid-column: 3/4;
    grid-row: 1/3;
  }
}